import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


function Square(props) {
    return (
        <button className={"square" + props.class} onClick={props.onClick}>
            {props.value}
        </button>
    );
}

class Board extends React.Component {
    renderSquare(i) {

        let classForSquare = "";
        if(i === this.props.iSignal)
        {
            classForSquare = " text-red";
        }

        let arr = this.props.arrayCellWin;

        console.log("winner: " + arr);


        if(arr !== null)
        {
            for(let k = 0;k < arr.length;k++){
                if(arr[k] === i){
                    classForSquare = classForSquare + " bg-yellow";
                    break;
                }
            }
        }

        return <Square key={i}
            value={this.props.squares[i]}
            onClick={() => this.props.onClick(i)}
            class = {classForSquare}
        />;
    }

    drawBoardWithInput() {

        const numInCol = this.props.numCol;
        let irun = 0;

        let listBoardRow = [];

        for(let i =0; i < numInCol; i++) {
            let listSquare = [];
            for (let t = 0; t < numInCol; t++) {
                listSquare.push(this.renderSquare(irun++));
            }

            listBoardRow.push(
                <div className="board-row" key={i}>
                    {listSquare}
                </div>
            );
        }

        return listBoardRow;
    }

    render() {
        return (
            <div>
                {this.drawBoardWithInput()}
            </div>
        );
    }
}

function KiemTraKyTuLienTiep(iStart, iEnd, step, numCheckOfWinner, squares, currCell, numCol){
    //console.log(iStart, iEnd,step, numCheckOfWinner, squares, currCell, numCol);

    let i = iStart;
    let n = iEnd;
    let consecutiveCount = 0;
    let arrayLighting = [];

    //console.log("check: ");
    for(i; i <= n; i += step){
        //console.log(i);
        if(i >= 0 && i < numCol*numCol  && squares[i] !== undefined){
            if(squares[i] === squares[currCell]){
                consecutiveCount++;
                arrayLighting.push(i);
                if(consecutiveCount === numCheckOfWinner)
                {
                    break;
                }
            }
            else{
                consecutiveCount = 0;
                arrayLighting = [];
            }
        }
        else{
            consecutiveCount = 0;
            arrayLighting = [];
        }
    }

    if(consecutiveCount !== numCheckOfWinner)
    {
        arrayLighting = null;
    }

    return arrayLighting;
}

function KiemTraNgang(iStart, iEnd, step, numCheckOfWinner, squares, currCell, numCol){
    //console.log(iStart, iEnd,step, numCheckOfWinner, squares, currCell, numCol);

    let i = iStart;
    let n = iEnd;
    let consecutiveCount = 0;
    let arrayLighting = [];

    for(i; i <= n; i += step){

        if(i >= 0 && i < numCol*numCol  && squares[i] !== undefined){
            let iXY = getXY(i,numCol);

            if(squares[i] === squares[currCell] && iXY.y === getXY(currCell,numCol).y){
                consecutiveCount++;
                arrayLighting.push(i);
                if(consecutiveCount === numCheckOfWinner)
                {
                    break;
                }
            }
            else{
                consecutiveCount = 0;
                arrayLighting = [];
            }
        }
        else{
            consecutiveCount = 0;
            arrayLighting = [];
        }
    }

    if(consecutiveCount !== numCheckOfWinner)
    {
        arrayLighting = null;
    }

    return arrayLighting;
}

