function KiemTraCheoTrai(iStart, iEnd, step, numCheckOfWinner, squares, currCell, numCol){

    let i = iStart;
    let n = iEnd;
    let consecutiveCount = 0;
    let arrayLighting = [];

    let x = getXY(currCell,numCol).x;
    let y = getXY(currCell,numCol).y;

    let temp = null;

    for(let k = 0; k < numCheckOfWinner; k++){
        if(x < k && x > (numCol-numCheckOfWinner) && y < k && y > (numCol-numCheckOfWinner)){
            console.log("khong thoa");
            return temp;
        }
    }

    for(i; i <= n; i += step){

        if(i >= 0 && i < numCol*numCol  && squares[i] !== undefined){
            let iXY = getXY(i,numCol);

            if(squares[i] === squares[currCell]){
                consecutiveCount++;
                arrayLighting.push(i);
                if(consecutiveCount === numCheckOfWinner)
                {
                    break;
                }
            }
            else{
                consecutiveCount = 0;
                arrayLighting = [];
            }
        }
        else{
            consecutiveCount = 0;
            arrayLighting = [];
        }
    }

    if(consecutiveCount !== numCheckOfWinner)
    {
        arrayLighting = null;
    }

    return arrayLighting;
}

function calculateWinner(squares, currCell, numCol, numCheckOfWinner) {

    //console.log("squares: " + squares + "  currCell: " + currCell + "  numCol: " + numCol + " numCheckOfWinner: " + numCheckOfWinner);

    let arrayLighting=[];

    // KIM TRA THNG NGANG
    let iStart = currCell - (numCol - 1);
    let iEnd = currCell + (numCol - 1);
    let step = 1;
    let result = KiemTraNgang(iStart,iEnd,step,numCheckOfWinner,squares, currCell, numCol);

    //console.log("hang ngang: " + result);

    if(result !== null)
    {
        arrayLighting = arrayLighting.concat(result);
    }

    // KIM TRA THNG DC
    iStart = currCell - numCol*4;
    iEnd = currCell + numCol*4;
    step = numCol;
    result = KiemTraDoc(iStart,iEnd,step,numCheckOfWinner,squares, currCell, numCol);

    //console.log("hang doc: " + result);

    if(result !== null)
    {
        arrayLighting = arrayLighting.concat(result);
    }

    // KIM TRA CHO TRI
    iStart = currCell - (numCol + 1)*4;
    iEnd = currCell + (numCol + 1)*4;
    step = numCol + 1;
    result = KiemTraCheoTrai(iStart,iEnd,step,numCheckOfWinner,squares, currCell, numCol);

    //console.log("cheo trai: " + result);

    if(result !== null)
    {
        arrayLighting = arrayLighting.concat(result);
    }

    // KIM TRA CHO PHI
    iStart = currCell - (numCol - 1)*4;
    iEnd = currCell + (numCol - 1)*4;
    step = numCol - 1;
    result = KiemTraKyTuLienTiep(iStart,iEnd,step,numCheckOfWinner,squares, currCell, numCol);

    //console.log("cheo phai: " + iStart, iEnd, step);

    if(result !== null)
    {
        arrayLighting = arrayLighting.concat(result);
    }

    let resultObj = {
        winner: null,
        cellLightArray: null,
    };

    if(arrayLighting.length > 0){
        resultObj = {
            winner: squares[currCell],
            cellLightArray: arrayLighting,
        }
    }

    //console.log("resultObj.winner: " + resultObj.winner+ " arrayLighting: " + resultObj.cellLightArray);

    return resultObj;
}

function getXY(value, numCol){
    return {
        x: value%numCol,
        y: parseInt(value/numCol, 10),
    };
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            history: [{
                squares: [],
                currCell: null,
            }],
            stepNumber: 0,
            xIsNext: true,
            numCol: 6,
            arrangeIsAsc: false,
            numCheckOfWinner: 5,
        };
    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if (calculateWinner(squares, current.currCell, this.state.numCol, this.state.numCheckOfWinner).winner || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? 'X' : 'O';
        this.setState({
            history: history.concat([{
                squares: squares,
                currCell: i,
            }]),
            stepNumber: history.length,
            xIsNext: !this.state.xIsNext,
        });
    }

    jumpTo(step) {
        this.setState({
            stepNumber: step,
            xIsNext: (step % 2) === 0,
        });
    }

    SetArrange(){
        this.setState({
            arrangeIsAsc: !this.state.arrangeIsAsc,
        })
    }

    render() {
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares, current.currCell, this.state.numCol, this.state.numCheckOfWinner).winner;
        const cellLightArray = calculateWinner(current.squares, current.currCell, this.state.numCol, this.state.numCheckOfWinner).cellLightArray;

        let movingList = history.slice();
        let varDec = 0;

        if(!this.state.arrangeIsAsc)
        {
            movingList = history.slice().reverse();
            varDec =  movingList.length - 1;
        }

        const moves = movingList.map((step, move) => {

            const toaDo = getXY(step.currCell, this.state.numCol);

            // console.log("move: " + move + "  varDec: " + varDec);

            move = Math.abs(varDec - move);


            const desc = move?
                'Go to move #' + move + " ("+ toaDo.x + "," + toaDo.y + ")":
                'Go to game start';

            let bg_red = "";
            if(move === history.length-1 && desc !== 'Go to game start')
            {
                bg_red= "bg-green";
            }

            return (
                <li key={move}>
                    <button className={bg_red} onClick={() => this.jumpTo(move)}>{desc}</button>
                </li>
            );
        });

        let status;
        if (winner) {
            status = 'Winner: ' + winner;
        } else {
            status = 'n lt: ' + (this.state.xIsNext ? 'X' : 'O');
        }

        return (
            <div>
                {/*<Input comState={this}/>*/}
                <div className="game">
                    <div className="game-board">
                        <Board
                            squares={current.squares}
                            onClick={(i) => this.handleClick(i)}
                            numCol={this.state.numCol}
                            iSignal={current.currCell}
                            arrayCellWin={cellLightArray}
                        />
                    </div>
                    <div className="game-info">
                        <div className={"status"}>{status}</div>
                        <button className="arrange-btn" onClick={() => this.SetArrange()}>Sp xp danh sch</button>
                        <ol>{moves}</ol>
                    </div>
                </div>
            </div>
        );
    }
}

class Input extends React.Component{
    getNumCol(){
        const state = this.props.comState;
        const valInput = document.getElementById("txtNumCol").value;
        state.setState({numCol: valInput});
    }

    render(){
        return (
            <div className="input-group input-content">
            <span className="input-group-addon">
                Input:
            </span>
                <input id="txtNumCol" type="text" className="form-control" placeholder="number col" />
                <span className="input-group-btn">
                <button className="btn btn-secondary" type="button" onClick={()=> this.getNumCol()}>
                    Bt u
                </button>
            </span>
            </div>
        );
    }

}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);


